
.block-attribute-wrapper {
  display: inline-block;
  margin-right: 20px;
}

.shortcode-ui-edit-shortcake_articles {
  display: flex;
  flex-wrap: wrap;
}

div.shortcode-ui-edit-shortcake_articles > div {
  padding-left: 2%;
  flex: 0 48%;
}

div.shortcode-ui-edit-shortcake_articles > div:nth-child(8) {
  align-self: flex-end;
}

div.shortcode-ui-edit-shortcake_articles > div:last-child {
  flex: 0 100%;
}

.hr-dashed {
  border-top: 1px dashed #ddd;
  border-bottom: 1px dashed #ddd;
}

select[id^="shortcode-ui-posts-"] + span li {
  display: block !important;
  float: none !important;
}

.shortcode-ui-edit-shortcake_articles > div {
  position: relative;
}

.shortcode-ui-edit-shortcake_newcovers {
  display: flex;
  flex-wrap: wrap;
}

div.shortcode-ui-edit-shortcake_newcovers > div {
  padding-left: 2%;
  flex: 0 55%;
}

@media (min-width: 768px) {
  div.shortcode-ui-edit-shortcake_newcovers > div {
    padding-left: 2%;
    flex: 0 47%;
  }
}

@media (min-width: 992px) {
  div.shortcode-ui-edit-shortcake_newcovers > div {
    padding-left: 2%;
    flex: 0 30%;
  }
}

div.shortcode-ui-edit-shortcake_newcovers > div:first-child {
  flex: 0 90%;
}

div.shortcode-ui-edit-shortcake_newcovers > div:last-child {
  flex: 0 90%;
}

.shortcode-ui-edit-shortcake_columns div[class*=shortcode-ui-attribute-title_] {
  border-top: 2px dashed #ddd;
  padding-top: 20px;
}

.field-caption {
  color: #aaa;
  font-style: italic;
  width: 60%;
}

.shortcode-ui-edit-shortcake_newcovers > div {
  position: relative;
}

.shortcake-p4-radio-div {
  display: inline-block;
  margin: 10px;
  padding: 20px;
  max-width: 25%;
  vertical-align: top;
}

.not-clickable {
  pointer-events: none;
}

#bl_loader {
  float: inherit;
}

#bl_loading_span {
  margin-left: 10px;
}

/* Carousel header */
.shortcode-ui-edit-shortcake_carousel_header .shortcake-p4-radio-div img {
  max-width: 100%;
}

/* CSS trick to make the images and description click the radio too. */
.shortcode-ui-edit-shortcake_carousel_header .shortcake-p4-radio-div {
  position: relative;
}

.shortcode-ui-edit-shortcake_carousel_header .shortcake-p4-radio-div label::before {
  display: block;
  content: " ";
  opacity: 0;
  width: 100%;
  position: absolute;
  height: 100%;
}

.shortcode-ui-edit-shortcake_carousel_header .field-block.shortcode-ui-field-text,
.shortcode-ui-edit-shortcake_carousel_header .field-block.shortcode-ui-field-url,
.shortcode-ui-edit-shortcake_carousel_header .field-block.shortcode-ui-field-url input,
.shortcode-ui-edit-shortcake_carousel_header .field-block.shortcode-ui-field-text input {
  width: 400px;
}

.max-length-counter {
  color: #aaaaaa;
  text-align: right;
}

.shortcode-ui-attribute-gallery_block_focus_points {
  display: none;
}
